import React, { useState } from 'react'
import { data } from '../data'
import { Link } from 'react-router-dom'
import './Home-Components/HomeC.css'
const Blog = () => {
  const [blog, setBlog] = useState(data)

  var today = new Date()
  var dd = String(today.getDate()).padStart(2, '0')
  var mm = String(today.getMonth() + 1).padStart(2, '0') //January is 0!
  var yyyy = today.getFullYear()
  today = mm + '/' + dd + '/' + yyyy

  return (
    <div>
      <div className='mb-4  full-bg'>
        <h1 className='display-4 text-white'>Blog</h1>
        <p className='lead text-warning '>
          Lorem ipsum dolor sit amet.
        </p>
      </div>
      <div className='card-deck row row-cols-1 row-cols-md-3 mt-4 '>
        {blog.map((post) => {
          const date1 = new Date(post.time)
          const date2 = new Date(today)
          const diffTime = Math.abs(date2 - date1)
          const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
          return (
            
            <div
              key={post.id}
              className='col col-sm-6 col-md-4 col-lg-3 mb-4'
            >
              <div className='card h-100 text-dark text-center c-bg border-dark mb-3 ps-bg'>
                <div className='card-body'>
                  <h2 className='card-title'>{post.name}</h2>
                  <hr/>
                  <p className='card-text text-dark'>{post.post}</p>
                  <Link to={`/post/${post.id}`} className='btn btn-dark'>
                    Read More
                    {/* <img src='...' alt='' width='70px' /> */}
                  </Link>
                </div>
                <div className='card-footer'>
                  <small className='text-dark'>
                    Updated {diffDays} days ago
                  </small>
                </div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default Blog
