import React from 'react'
import { Link } from 'react-router-dom'
import './HomeC.css'

const HSecond = () => {
  return (
    <div className='row justify-content-center'>
      <div
        className='card mb-3 border border-dark hs-bg'
        style={{ maxWidth: '35rem' }}
      >
        <div className='row no-gutters'>
          <div className='col-md-4'>
            <img
              src='https://i.pinimg.com/originals/df/25/e3/df25e39d7400f1bbcb7adf695063faaa.jpg'
              className='card-img '
              alt='...'
            />
          </div>
          <div className='col-md-8'>
            <div className='card-body'>
              <h5 className='card-title'>About</h5>
              <p className='card-text text-left text-dark'>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nam,
                quibusdam adipisci. Eos porro expedita mollitia.
              </p>
              <Link to='/about' className='btn'>
                About
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div
        className='card mb-3 ml-1 border border-dark hs-bg-r'
        style={{ maxWidth: '35rem' }}
      >
        <div className='row no-gutters'>
          <div className='col-md-8'>
            <div className='card-body'>
              <h5 className='card-title'>My Posts</h5>
              <p className='card-text text-left text-dark'>
                This is a wider card with supporting text below as a natural
                lead-in to additional content. This content is a little bit
                longer.
              </p>
              <Link to='/blog' className='btn'>
                Posts
              </Link>
            </div>
          </div>
          <div className='col-md-4'>
            <img
              src='https://cdn.wallpapersafari.com/66/51/8tNDXE.jpg'
              className='card-img '
              alt='...'
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default HSecond
