import React from 'react'
import { Link } from 'react-router-dom'
import './About-Components/AboutC.css'

const About = () => {
  return (
    <div className='row justify-content-center'>
      <div className='card  ab-one'>
        <img
          className='card-img-top'
          src='https://i.pinimg.com/originals/df/25/e3/df25e39d7400f1bbcb7adf695063faaa.jpg'
          alt='Card image cap'
        />
        <div className='card-body'>
          <h2 className='card-title'>Lorem, ipsum.</h2>
        </div>
      </div>

      <div className='card ab-two'>
        <div className='card-body'>
          <h5 className='card-title'>Some Title</h5>
          <p className='card-text text-left'>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Laboriosam,
            deleniti quas, ipsam consequuntur velit laborum nostrum minima rem
            optio error aspernatur natus sapiente, odio necessitatibus eaque
            suscipit sed. Provident architecto dignissimos veniam blanditiis
            illo, tenetur sit fugiat aliquam maxime consequuntur totam laborum
            sed nam, pariatur, necessitatibus natus amet cumque. Eum.
          </p>
          <Link to='/blog' className='btn'>
            Read my Posts
          </Link>
        </div>
      </div>
    </div>
  )
}

export default About
